    z-index: -1;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3.125rem;
    background-color: #ff6b6b;
    content: ''; }
  @media only screen and (min-width: 28.125rem) {
